															</view>
															<view class="stoneright" style='font-weight: 700;'>
																{{item.zwlist.occupyRate}}
															</view>
														</view>
														<view class="stone">
															<view class="stoneleft" style='font-weight: 700;'>
																占位费用:
															</view>
															<view class="stoneright" style='font-weight: 700;'>
																{{item.zwlist.occupyMoney}}
