																<span v-show='item.zwlist.statuss==0'>元</span>
																<span v-show='item.zwlist.statuss==1'>小时</span>
															</view>
														</view>
													</a>
													<view class="stonebtn" @click="kkbtn(item.orderNum,item.userId)"
														v-show='(item.amount>0||item.realMoney>0)&&item.kuanno!=1'>
														扣款详情
													</view>
													<view class="stonebtn" style="width: 170upx;margin-top: 10upx;"
