													<view class="stoneleft">订单状态：
														<span v-show="item.isSettlement=='0'">未结算</span>
														<span v-show="item.isSettlement=='1'">已结算</span>
														<span v-show="item.isSettlement=='2'">费率异常导致结算异常</span>
														<span v-show="item.isSettlement=='3'">离线订单跨公众号异常</span>
														<span v-show="item.isSettlement=='4'">离线订单未绑定用户异常</span>
														<span v-show="item.isSettlement=='5'">设备被绑定充电区</span>
														<span v-show="item.isSettlement=='6'">包月扣除失败</span>
													</view>
													<view class="stoneleft">启动方式：
														<span v-show="item.qrOrCard=='1'">电卡</span>
														<span v-show="item.qrOrCard=='2'">扫码</span>
														<span v-show="item.qrOrCard=='3'">验证码</span>
														<span v-show="item.qrOrCard=='4'">蓝牙</span>
													</view>
													<view class="stoneleft">结束原因：
														<span v-if="item.stopReason=='01'">
															充满自停
														</span>
														<span v-else-if="item.stopReason=='02'">
															达到最大充电时长
														</span>
														<span v-else-if="item.stopReason=='03'">
															达到预设时间
														</span>
														<span v-else-if="item.stopReason=='04'">
															达到预设电量
														</span>
														<span v-else-if="item.stopReason=='05'">
															<span v-if="item.deviceId.substr(-2, 2)=='04'">用户拔出</span>
															<span v-else>充电结束</span>
														</span>
														<span v-else-if="item.stopReason=='06'">
															负载过大
														</span>
														<span v-else-if="item.stopReason=='07'">
															手动停止
														</span>
														<span v-else-if="item.stopReason=='08'">
															功率起伏过大
														</span>
														<span v-else-if="item.stopReason=='09'">
															电池未连接好
														</span><span v-else-if="item.stopReason=='10'">
															水浸断电
														</span>
														<span v-else-if="item.stopReason=='11'">
															灭火结算（本端口）
														</span>
														<span v-else-if="item.stopReason=='12'">
															灭火结算（非本端口）
														</span>
														<span v-else-if="item.stopReason=='13'">
															用户密码开柜断电
														</span>
														<span v-else-if="item.stopReason=='14'">
															未关好柜门
														</span>
														<span v-else-if="item.stopReason=='15'">
															外部操作打开柜门
														</span>
														<span v-else-if="item.stopReason=='17'">
															服务器强制停止
														</span>
														<span v-else-if="item.stopReason=='0A'">
															环境温度过高
														</span>
														<span v-else-if="item.stopReason=='0B'">
															端口温度过高
														</span>
														<span v-else-if="item.stopReason=='0C'">
															过流或短路
														</span>
														<span v-else-if="item.stopReason=='0D'">
															用户拔出-1
														</span>
														<span v-else-if="item.stopReason=='0E'">
															接触不良或保险丝烧断
														</span>
														<span v-else-if="item.stopReason=='0F'">
															继电器坏或保险丝断
														</span>
														<span v-else>
															{{item.stopReason}}
														</span>
													</view>
													<view class="stoneleft">小区名称：{{item.projectName}}</view>
													<view class="stoneleft">充电区名称：{{item.areaName}}</view>
												</view>

												<view class="sttwo">
													<van-collapse v-model="activeNames2" accordion
														@change="zxbtn(item.id)">
														<van-collapse-item :name='item.orderNum'>
															<template #title>
																<view class="hmtxt">功率曲线</view>
															</template>
															<view class="qiun-columns" v-show="typeshow==1">
																<view class="qiun-charts">
																	<canvas :canvas-id='item.id' :id='item.id'
																		class="charts"
																		@touchstart="touchLineA"></canvas>
																</view>
															</view>
															<view class="failshow" v-show="typeshow==2">
																<view class="flone">{{showtxt}}</view>
															</view>
														</van-collapse-item>
													</van-collapse>
												</view>
												<view class="stthree">
													<view class="stthreefirst">
														温馨提示
													</view>
													<view class="stthreesecond">
														<view>1.功率曲线越平稳，说明电瓶性能越好</view>
														<view>2.开始充电，结束时曲线波动大属于正常现象</view>
														<view>3.设备故障，电瓶中途被拔，网络异常等极端情况绘制曲线会不准确哦</view>
													</view>
												</view>
											</view>
										</van-collapse-item>
									</van-cell>
								</van-list>
							</van-pull-refresh>
						</van-collapse>
					</view>
				</van-tab>
			</van-tabs>

		</view>
		<date-picker :show="showPicker" :type="type" class="mask" :value="value" :show-tips="true" :begin-text="'开始'"
			:end-text="'结束'" :show-seconds="true" @confirm="onSelected" @cancel="onCancel"></date-picker>
	</view>
</template>

<script>
	// import uCharts from '../../components/u-charts/u-charts.js';
	var _self;
	var canvaLineA = null;
	import tou_hand1 from '../../components/tou_hand1/tou_hand1.vue'
	import {
		formatDate
	} from '../../common/formatDate.js'
	import datePicker from "../../components/mx-datepicker.vue";

	export default {
		components: {
			tou_hand1,
			datePicker
		},
		mounted() {

		},

		data() {
			return {
				active: 'a',
				projectId: '', //小区ID
				activeNames: [''], //当前展开面板的 name
				activeNames1: [''], //当前展开面板的 name
				activeNames2: [''],
				activeNames3: [''],
				value: ['请选择开始时间', '请选择结束时间'],
				type: 'range', //时间选择器
				showPicker: false, //时间选择器
				blist: [], //本区消费数据
				xlist: [], //临时消费数据
				klist: [], //跨区消费数据
				list: [], //临时充电
				loading: false,
				finished: false,
				refreshing: false,
				pageNum: 1, //页码
				pageSize: 10, //每页显示的条数
				yloading: false,
				yfinished: false,
				yrefreshing: false,
				ypageNum: 1, //页码
				ypageSize: 10, //每页显示的条数
				xloading: false,
				xfinished: false,
				xrefreshing: false,
				xpageNum: 1, //页码
				xpageSize: 10, //每页显示的条数
				cWidth: '',
				cHeight: '',
				pixelRatio: 1,
				typeshow: 1,
				showtxt: "",
				hat: {},
				userId: '', //用户id
				freeappid:''
			}
		},
		onLoad(option) {
			this.freeappid=uni.getStorageSync('user').appid
			this.projectId = option.projectId
			this.show();
			this.loadMore();
			_self = this;
			this.cWidth = uni.upx2px(686);
			this.cHeight = uni.upx2px(500);
		},
		methods: {
			show() {

				var nowtime = formatDate(new Date(), 'yyyy-MM-dd');
				var oldtime = formatDate(new Date(new Date().getTime() - 2592000000), 'yyyy-MM-dd');
				this.value = [oldtime, nowtime]
			},
			gbtn(vul) {
				this.userId = ''
				this.activeNames2 = []
				this.activeNames = []
				this.activeNames1 = []
				this.activeNames3 = []
				if (vul == 'a') {
					this.blist = [];
					this.onRefresh()
				} else if (vul == 'b') {
					this.klist = [];
					this.yonRefresh()
				} else if (vul == 'c') {
					this.xlist = [];
					this.xonRefresh()
				}
			},
			// 本区消费分页
			loadMore() {
				if (this.refreshing) {
					this.blist = [];
					this.refreshing = false;
				}
				const that = this;
				let data = {
					projectIds: this.projectId,
					type: 1,
					userId: this.userId,
					pageNum: that.pageNum,
					pageSize: that.pageSize,
					startTime: that.value[0],
					endTime: that.value[1]
				}
				that.loading = true;
				that.$base.request1('manager/statistic/consume/list', 'GET', data)
					.then(res => {
						that.loading = false;
						if (res.data.rows && res.data.rows.length > 0) {
							for (var i = 0; i < res.data.rows.length > 0; i++) {
								res.data.rows[i].userId = this.pad(res.data.rows[i].userId, 8)
								res.data.rows[i].zwno = 0
								res.data.rows[i].zwlist = {}
							}
							that.blist = that.blist.concat(res.data.rows);
							that.pageNum += 1;
						} else {
							that.finished = true;
						}
					})
					.catch(err => {

					})
			},
			onRefresh() {
				// 清空列表数据
				this.finished = false;
				this.pageNum = 1
				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loading = true;
				this.loadMore();
			},

			// 跨区消费分页
			yloadMore() {
				if (this.yrefreshing) {
					this.klist = [];
					this.yrefreshing = false;
				}
				const that = this;
				let data = {
					projectIds: this.projectId,
					type: 2,
					userId: this.userId,
					pageNum: that.ypageNum,
					pageSize: that.ypageSize,
					startTime: that.value[0],
					endTime: that.value[1]
				}
				that.yloading = true;
				that.$base.request1('manager/statistic/consume/list', 'GET', data)
					.then(res => {
						that.yloading = false;
						if (res.data.rows && res.data.rows.length > 0) {
							for (var i = 0; i < res.data.rows.length > 0; i++) {
								res.data.rows[i].userId = this.pad(res.data.rows[i].userId, 8)
								res.data.rows[i].zwno1 = 0
								res.data.rows[i].zwlist1 = {}
							}
							that.klist = that.klist.concat(res.data.rows);
							that.ypageNum += 1;
						} else {
							that.yfinished = true;
						}
					})
					.catch(err => {

					})
			},
			yonRefresh() {
				// 清空列表数据
				this.yfinished = false;
				this.ypageNum = 1
				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.yloading = true;
				this.yloadMore();
			},
			// 临时充电
			xloadMore() {
				if (this.xrefreshing) {
					this.xlist = [];
					this.xrefreshing = false;
				}
				const that = this;
				let data = {
					projectIds: this.projectId,
					type: 0,
					userId: this.userId,
					pageNum: that.xpageNum,
					pageSize: that.xpageSize,
					startTime: that.value[0],
					endTime: that.value[1]
				}
				that.xloading = true;
				that.$base.request1('manager/statistic/consume/list', 'GET', data)
					.then(res => {
						that.xloading = false;
						if (res.data.rows && res.data.rows.length > 0) {
							for (var i = 0; i < res.data.rows.length > 0; i++) {
								res.data.rows[i].userId = this.pad(res.data.rows[i].userId, 8)
							}
							that.xlist = that.xlist.concat(res.data.rows);
							that.xpageNum += 1;
						} else {
							that.xfinished = true;
						}
					})
					.catch(err => {

					})
			},
			xonRefresh() {
				// 清空列表数据
				this.xfinished = false;
				this.xpageNum = 1
				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.xloading = true;
				this.xloadMore();
			},
			//按照用户编号搜索
			sbtn(vul) {
				this.activeNames2 = []
				this.activeNames = []
				this.activeNames1 = []
				this.activeNames3 = []
				if (vul == 1) {
					this.blist = [];
					this.onRefresh()
				} else if (vul == 2) {
					this.klist = [];
					this.yonRefresh()
				} else if (vul == 3) {
					this.xlist = [];
					this.xonRefresh()
				}
			},
			//选择时间点击确定
			onSelected(e) { //选择
				this.showPicker = false;
				if (e) {
					this[this.type] = e.value;
					for (var i = 0; i < e.value.length; i++) {
						e.value[i] = formatDate(new Date(e.value[i]), 'yyyy-MM-dd')
					}
					this.value = e.value;
					if (this.active == 'a') {
						this.blist = [];
						this.onRefresh()
					} else if (this.active == 'b') {
						this.klist = [];
						this.yonRefresh()
					}
				}
			},
			//选择时间点击取消
			onCancel() {
				this.showPicker = false;
				var nowtime = formatDate(new Date(), 'yyyy-MM-dd');
				var oldtime = formatDate(new Date(new Date().getTime() - 2592000000), 'yyyy-MM-dd');
				this.value = [oldtime, nowtime]
				if (this.active == 'a') {
					this.blist = [];
					this.onRefresh()
				} else if (this.active == 'b') {
					this.klist = [];
					this.yonRefresh()
				}
			},
			//点击时间框选择时间
			onShowDatePicker(type) { //显示
				this.type = type;
				this.showPicker = true;
				this.value = this[type];
			},
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			},
			getServerData() {
				let LineA = _self.chartData
				_self.showLineA("canvasLineA", LineA);
			},
			showLineA(canvasId, chartData) {
				canvaLineA = new uCharts({
					$this: _self,
					canvasId: canvasId,
					type: 'line',
					fontSize: 11,
					legend: {
						show: false
					},
					dataLabel: false,
					dataPointShape: true,
					background: '#FFFFFF',
					pixelRatio: _self.pixelRatio,
					categories: chartData.categories,
					series: chartData.series,
					animation: true,
					xAxis: {
						type: 'grid',
						gridColor: '#CCCCCC',
						gridType: 'dash',
						dashLength: 8,
						disableGrid: false,
						labelCount: 4
					},
					yAxis: {
						gridType: 'dash',
						gridColor: '#CCCCCC',
						dashLength: 8,
						splitNumber: 5,
						min: 10,
						max: 180,
						format: (val) => {
							return val.toFixed(0)
						}
					},
					width: _self.cWidth * _self.pixelRatio,
					height: _self.cHeight * _self.pixelRatio,
					extra: {
						line: {
							type: 'straight'
						}
					}
				});

			},
			touchLineA(e) {
				var that = this
				let legendIndex = canvaLineA.getCurrentDataIndex(e)
				canvaLineA.showToolTip(e, {
					textList: [{
						text: '功率:'
					}, {
						text: '电压:'
					}, {
						text: '端口温度:'
					}, {
						text: '时间:'
					}],
					format: function(item, category) {
						var tap = that.hat.series[0]
						this.textList = [{
							text: '功率 : ' + tap.power[legendIndex] + 'W'
						}, {
							text: '电压 : ' + tap.voltage[legendIndex] + 'V'
						}, {
							text: '时间 : ' + tap.time[legendIndex]
						}]
						if (tap.portTemperature[legendIndex] != '无数据') {
							this.textList.push({
								text: '端口温度 : ' + tap.portTemperature[legendIndex] + '度'
							})
						} else {
							this.textList.push({
								text: '端口温度 : ' + tap.portTemperature[legendIndex]
							})
						}
						if (tap.chargeTime[legendIndex]) {
							this.textList.push({
								text: '充电总时长 : ' + tap.chargeTime[legendIndex]
							})
						}

					}
				});
			},

			zxbtn(id) {
				uni.showLoading({
					title: '请稍候'
				})
				this.$base.request1('powerCurve' + '/' + id, 'GET')
					.then(res => {
						if (res.data.code == 200) {
							uni.hideLoading()
							var arr = res.data.data.powerArr
							var ztime = ''
							if (res.data.data.chargeTime) {
								ztime = res.data.data.chargeTime
							}
							var arr1 = []
							var power = [] //功率（单位0.1瓦）
							var voltage = [] //电压
							var temperature = [] //环境温度
							var portTemperature = [] //端口温度
							var chargeTime = []
							var time = [] //时间
							for (var i = 0; i < arr.length; i++) {
								var disLength = arr[i].time.length;
								var shortName = arr[i].time.substring(disLength - 8, disLength);

								arr1.push(shortName)
								power.push(arr[i].power / 10)
								voltage.push(arr[i].voltage / 10)
								temperature.push(arr[i].temperature)
								portTemperature.push(arr[i].portTemperature)
								time.push(arr[i].time)
								if (ztime) {
									chargeTime.push(ztime)
								}
							}
							var map1 = {}
							map1['name'] = ''
							map1['data'] = power
							map1['power'] = power
							map1['voltage'] = voltage
							map1['temperature'] = temperature
							map1['portTemperature'] = portTemperature
							map1['time'] = time
							map1['chargeTime'] = chargeTime
							var arr3 = []
							arr3.push(map1)
							var map2 = {}
							map2['categories'] = arr1
							map2['series'] = arr3
							this.typeshow = 1
							this.hat = map2
							_self.showLineA(id, map2);
						} else {
							uni.hideLoading()
							this.typeshow = 2
							this.showtxt = res.data.msg
						}

					})
					.catch(err => {

					})

			},
			pad(num, n) {
				var len = num.toString().length;
				while (len < n) {
					num = "0" + num;
					len++;
				}
				return num;
			},
			//点击扣款详情
			kkbtn(orderNum, userId) {
				let data = {
					userId: userId,
					orderNum: orderNum
				}
				let kklist = []
				uni.showLoading({
					title: '请稍候',
					mask: true
				})
				this.$base.request1('userAccountDetail/info', 'GET', data)
					.then(res => {
						if (res.data.code == 200) {
							uni.hideLoading()
							if (res.data.data.length > 0) {
								kklist = res.data.data
								for (var i = 0; i < this.blist.length; i++) {
									if (this.blist[i].orderNum == orderNum) {
										if (this.blist[i].amount > 0 || this.blist[i].realMoney > 0) {
											this.blist[i].kuanno = 1
											this.blist[i].kklist = kklist
										}
									}
								}
								this.loadMore();
							} else {
								this.$toast.fail('未查询到消费扣款详情')
							}
						} else {
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {

					})

			},
			//点击占位费详情
			zwbtn(orderNum) {
